<template>
	<table cellspacing="20" cellpadding="5">
		<tr v-for="i in 3">
			<td v-for="j in 4">
				<label class="chapter_card">
					<input class="chapter_card__input" type="checkbox" @click='checkedOne((i==1?j:i==2?j+4:j+8)-1)' :checked="currentId==(i==1?j:i==2?j+4:j+8)-1">
					<div class="chapter chapter_card__body">
						<img class="small-chapter" :src="require(`@/assets/images/chapter/${(i==1?j:i==2?j+4:j+8)-1}.jpg`)"/>
						<center><div>{{$t('chapterName',[i==1?j:i==2?j+4:j+8])}}</div></center>
					</div>
				</label>
			</td>
		</tr>
	</table>
</template>

<script>
	export default {
		name: 'chapter',
		props:['datas'],
		data(){
			return {
				currentId:-1,
			}
		},
		methods:{
			checkedOne (id) {
				this.currentId=id;
				this.datas.chapter = id;
			},
		},
		mounted () {
		}
	}
</script>

<style>
	.small-chapter {
		height: 180px;
		width: 180px;
		object-fit: cover;
		border-radius: 12px;
		-webkit-border-radius: 12px;
		-moz-border-radius: 12px;
	}
	
	.chapter {
	   display: inline-flex;
		flex-direction: column;
		/* display: inline-block; */
	    color: white;
	    text-decoration: unset;
	}
	
	.chapter:hover {
	    /* text-shadow: -2px 0px 20px rgb(173 162 126 / 80%); */
	    background-color: #eff2f51f;
	    border-radius: 10px;
	    text-decoration: underline;
	}
	
	.chapter_card {
	  --background: #fff;
	  /* --background-chackbox: #0082ff; */
	  --background-image: #fff, rgba(0, 107, 175, 0.2);
	  --text-color: #666;
	  --text-headline: #000;
	  --card-shadow: #ffffff;
	  --card-height: 200px;
	  --card-witght: 180px;
	  --card-radius: 12px;
	  --header-height: 47px;
	  --blend-mode: overlay;
	  --transition: 0.15s;
	  -webkit-user-select: none;
	     -moz-user-select: none;
	      -ms-user-select: none;
	          user-select: none;
	}
	.chapter_card__body {
	  /* display: grid;
	  grid-auto-rows: calc(var(--card-height) - var(--header-height)) auto; */
	  height: var(--card-height);
	  width: var(--card-witght);
	  border-radius: var(--card-radius);
	  overflow: hidden;
	  position: relative;
	  cursor: pointer;
	  box-shadow: var(--shadow, 0 30px 30px 0 rgba(0, 0, 0, 0.02));
	  -webkit-transition: box-shadow var(--transition), -webkit-transform var(--transition);
	  transition: box-shadow var(--transition), -webkit-transform var(--transition);
	  transition: transform var(--transition), box-shadow var(--transition);
	  transition: transform var(--transition), box-shadow var(--transition), -webkit-transform var(--transition);
	  -webkit-transform: scale(var(--scale, 1)) translateZ(0);
	          transform: scale(var(--scale, 1)) translateZ(0);
	}
	.chapter_card__body:active {
	  --scale: 0.96;
	}
	.chapter_card__input {
	  position: absolute;
	  display: block;
	  outline: none;
	  border: none;
	  background: none;
	  padding: 0;
	  margin: 0;
	  -webkit-appearance: none;
	}
	.chapter_card__input:checked ~ .chapter_card__body {
	  --shadow: 0 0 0 3px var(--card-shadow);
	}
	.chapter_card__input:disabled ~ .chapter_card__body:active {
	  --scale: 1;
	}
	.chapter_card__input:disabled ~ .chapter_card__body {
	  cursor: not-allowed;
	  opacity: 0.5;
	}
</style>
