import Vue from 'vue'
import App from './App.vue'
import './fonts.css'

Vue.config.productionTip = false

//i18n
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)
// 自动根据浏览器系统语言设置语言

let lang = navigator.systemLanguage || navigator.language || "zh";
//en english
//zh-CN，zh chinese simple
//ja japanese
// console.log(lang,lang.toLowerCase())

const messages = {
	'zh':require("./locale/zh").default,
	'zh-CN':require("./locale/zh").default,
	'en':require("./locale/en").default,
	'en-US':require("./locale/en").default,
	'ja':require("./locale/ja").default
}
const i18n = new VueI18n({
  // locale: 'ja',
  locale: lang,
  fallbackLocale: 'en',//没找找到资源下默认的国际化，默认英文语言
  messages,
  silentTranslationWarn: true //去除控制台warn告警
})
// document.title = i18n.t("title")

new Vue({
  render: h => h(App),
  i18n
}).$mount('#app')

