<template>
	<div id="app">
		<saveparser :hidden="!hidden"/>
		<saveedit :hidden="hidden" />
		<img v-cloak class="sunnyimg" />
		<div class="changeBtn" @click="change"/>
	</div>
</template>

<script>
	import saveparser from './components/saveparser.vue'
	import saveedit from './components/saveedit.vue'

	export default {
		name: 'app',
		components: {
			saveparser,
			saveedit
		},
		data () {
			return {
					hidden: true,
			    }
		},
		methods: {
			change(){
				this.hidden = !this.hidden;
				//问题现象：切换图片会闪烁原因是第二个图片没有预加载，在js切换到它的时候没有加载完成，导致出现闪屏
				//解决办法：先预加载图片，再添加效果，定义一个img标签让它预加载
				//v-cloak指令可以像CSS选择器一样绑定一套CSS样式然后这套CSS会一直生效到实例编译结束。https://zhuanlan.zhihu.com/p/22477904
				// var theme = document.body.classList.contains("darkimg")? "sunnyimg" : "darkimg";
				this.setTheme();
			},
			setTheme(){
				document.title = this.hidden ? this.$t("title1") : this.$t("title2");
				document.body.className = this.hidden?"darkimg" : "sunnyimg";
			}
	    },
		mounted() {
			this.setTheme();
		}
	}
</script>

<style>
	body {
		color: white;
		/* font-family: 'Pattaya', sans-serif; */
		/* font-family: 'Patrick Hand SC', cursive; */
		font-family: 'Vollkorn SC', cursive;
		-webkit-background-size: cover;
		-moz-background-size: cover;
		-o-background-size: cover;
		background-size: cover;
	}
	.darkimg {
		background: url(./assets/images/cmatw_dark.png) no-repeat center center fixed;
	}
	.sunnyimg {
		background: url(./assets/images/cmatw_sunny.png) no-repeat center center fixed;
	}
	[v-cloak] {
	  display: none;
	}

	.changeBtn {
		position: fixed;
		bottom: 15px;
		right: 15px;
		width: 40px;
		height: 40px;
		line-height: 60px;
		/* text-align: center; */
		/* color: #ffffff; */
		/* z-index: 999; */
		/* background: #ffffff; */
		cursor: pointer;
		border-radius: 30px;
		box-shadow: 0px 0px 100px #424242;
	}
	
</style>
