<template>
	<div>
		<!-- 左箭头 -->
		<div class="robe_inline">
			<button class="robe_button glass clear-a" @click="previous()">&lt;</button>
		</div>
		<div class="robe_inline">
			<img :src="currentImg"/>
		</div>
		<!-- 右箭头 -->
		<div class="robe_inline">
			<button class="robe_button glass clear-a" @click="next()">&gt;</button>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'robe',
		props: ['datas'],
		data() {
			var robes = ["red0","red1","red2","red3","white1","white2","white3"];
			var imgPath = [];
			robes.forEach((e, i) => {
				imgPath[i]=require(`@/assets/images/robe/${e}.png`)
			});
			return {
				imgPath: imgPath,
				currentImg: require(`@/assets/images/robe/white1.png`),
				currentIndex: 4
			}
		},
		methods:{
			previous() {
				this.currentIndex--;
				if(this.currentIndex<0) this.currentIndex = this.imgPath.length-1;
				// console.log(this.currentIndex,this.imgPath.length,this.currentIndex<this.imgPath.length);
				this.datas.robe = this.currentIndex;
				this.currentImg = this.imgPath[this.currentIndex];
			},
			next() {
				this.currentIndex++
				if(this.currentIndex>=this.imgPath.length) this.currentIndex = 0;
				// console.log(this.currentIndex,this.imgPath.length,this.currentIndex<this.imgPath.length);
				this.datas.robe = this.currentIndex;
				this.currentImg = this.imgPath[this.currentIndex];
			}
		},
	}
</script>

<style>
	.robe_inline {
		  display: table-cell;
		  vertical-align: middle;
	}

	.robe_button {
		position: relative;
		color: rgba(255, 255, 255, 0.6);
		font-size: 20px;
		text-decoration: none;
		padding: 0.2em 1.5em;
		margin: 0.5em;
		cursor: pointer;
		width: -10px;
		height: 100px;
		border-radius: 8px;
		-webkit-border-radius: 8px;
		-moz-border-radius: 8px;
		-webkit-transition: all 0.1s ease-in-out;
		-moz-transition: all 0.1s ease-in-out;
		-o-transition: all 0.1s ease-in-out;
		transition: all 0.1s ease-in-out;
		outline: 0;
	}
</style>
