<template>
	<div>
		<center>
			<button class="button glass clear-a" type="button" @click="view = 'chapter'">{{$t('chapter')}}</button>
			<button class="button glass clear-a" type="button" @click="view = 'robe'">{{$t('robe')}}</button>
			<button class="button glass clear-a" type="button" @click="view = 'scarf'">{{$t('scarf')}}</button>
			<button class="button glass clear-a" type="button" @click="view = 'symbols'">{{$t('symbols')}}</button>
		</center>
		<div class="inline">
			<div class="leftinfo">
				<div>
					{{$t('levelName')}}：{{chapter}}<br />
					{{$t('robeName')}}：{{robe}}<br />
					{{$t('scarfName')}}：{{datas.scarf}}{{$t('scarfinfo')}}<br />
					{{$t('symbolsName')}}：<img class="small-symbol-view" :src="require(`@/assets/images/symbol/${datas.symbols}.svg`)" /><br />
				</div>
			</div>
			<div class="righttab">
				<div>
					<center>
						<transition name="fade" mode="out-in">
							<component v-bind:is="view" v-bind:datas="datas"></component>
						</transition>
					</center>
				</div>
			</div>
		</div>
		<center>
			<button class="button glass clear-a" type="button" @click="download()">{{$t('download')}}</button>
		</center>
	</div>
</template>

<script>
	import chapter from './saveeditor/chapter'
	import robe from './saveeditor/robe'
	import scarf from './saveeditor/scarf'
	import symbols from './saveeditor/symbol'
	export default {
		name: 'saveedit',
		components: {
			chapter,
			robe,
			scarf,
			symbols
		},
		data() {
			var ab = new ArrayBuffer(32768)
			var iA = new Int8Array(ab)
			iA[0] = parseInt(0x16);
			iA[3] = parseInt(0x80);
			iA[44] = parseInt(0xE6);
			iA[45] = parseInt(0x7);
			iA[46] = parseInt(0x3);
			iA[48] = parseInt(0xF);
			iA[50] = parseInt(0x11);
			iA[52] = parseInt(0x15);
			iA[54] = parseInt(0xF);
			iA[56] = parseInt(0x63);
			iA[57] = parseInt(0x2);
			iA[64] = parseInt(0xB0);
			iA[65] = parseInt(0xB2);
			iA[66] = parseInt(0x81);
			iA[67] = parseInt(0x13);
			iA[68] = parseInt(0x91);
			iA[69] = parseInt(0x38);
			iA[70] = parseInt(0xD8);
			iA[71] = parseInt(0x1);
			iA[72] = parseInt(0x1);
			iA[88] = parseInt(0x1C);
			iA[89] = parseInt(0x4B);
			iA[90] = parseInt(0xAD);
			iA[91] = parseInt(0x43);
			return {
				view: "chapter",
				datas: {
					chapter: 0,
					robe: 4,
					scarf: 0,
					symbols: 0
				},
				chapter: this.$t('chapter1'),
				robe: this.$t('while1'),
				savebin: iA
			}
		},
		methods: {
			download() {
				var _self = this;
				this.savebin[8] = this.datas.robe//长袍
				this.savebin[12] = this.datas.symbols//符文
				this.savebin[16] = this.datas.scarf//围巾
				this.savebin[4] = this.savebin[24] = this.datas.chapter//关卡
				var blob = new Blob([this.savebin], {type: "application/octet-binary"});//把二进制的码转化为blob类型
				const reader = new FileReader();
				reader.onload = (e) => {
					// console.log(e.target.result)
				    const a = document.createElement('a');
				    a.download = "SAVE.BIN";
				    a.href = e.target.result;
				    document.body.appendChild(a);
				    a.click();
				    document.body.removeChild(a);
				}
				reader.readAsDataURL(blob);
			}
		},
		watch: {
			'datas.chapter': function() {
				switch (this.datas.chapter) {
					case 0:
						this.chapter = this.$t('chapter1')
						break;
					case 1:
						this.chapter = this.$t('chapter2')
						break;
					case 2:
						this.chapter = this.$t('chapter3')
						break;
					case 3:
						this.chapter = this.$t('chapter4')
						break;
					case 4:
						this.chapter = this.$t('chapter5')
						break;
					case 5:
						this.chapter = this.$t('chapter6')
					 break;
					case 6:
						this.chapter = this.$t('chapter7')
						break;
					case 7:
						this.chapter = this.$t('chapter8')
						break;
					case 8:
						this.chapter = this.$t('chapter9')
						break;
					case 9:
						this.chapter = this.$t('chapter10')
						break;
					case 10:
						this.chapter = this.$t('chapter11')
						break;
					case 11:
						this.chapter = this.$t('chapter12')
						break;
				}
			},
			'datas.robe': function() {
				switch (this.datas.robe) {
					case 0:
						this.robe = this.$t('red1')
						break;
					case 1:
						this.robe = this.$t('red2')
						break;
					case 2:
						this.robe = this.$t('red3')
						break;
					case 3:
						this.robe = this.$t('red4')
						break;
					case 4:
						this.robe = this.$t('while1')
						break;
					case 5:
						this.robe = this.$t('while2')
						break;
					case 6:
						this.robe = this.$t('while3')
						break;
				}
			}
		}
	}
</script>

<style>
	.fade-enter-active,
	.fade-leave-active {
		transition: opacity .5s ease;
	}

	.fade-enter,
	.fade-leave-to {
		opacity: 0;
	}

	.btns:focus {
		outline: none;
	}

	.inline {
		display: table;
		width: 80%;
	}

	.leftinfo {
		font-family: 'Patrick Hand SC', cursive;
		display: table-cell;
		width: 20%;
		vertical-align: middle;
	}

	.righttab {
		display: table-cell;
		width: 80%;
	}

	.button {
		position: relative;
		color: rgba(255, 255, 255, 0.6);
		font-size: 20px;
		text-decoration: none;
		padding: 0.2em 1.5em;
		margin: 0.5em;
		cursor: pointer;
		width: 200px;
		height: 40px;
		border-radius: 8px;
		-webkit-border-radius: 8px;
		-moz-border-radius: 8px;
		-webkit-transition: all 0.1s ease-in-out;
		-moz-transition: all 0.1s ease-in-out;
		-o-transition: all 0.1s ease-in-out;
		transition: all 0.1s ease-in-out;
		outline: 0;
		text-align: middle;
	}

	.glass {
		background: linear-gradient(to bottom, rgba(180, 180, 180, 0.8) 0%, rgba(180, 180, 180, 0.5) 3%, rgba(180, 180, 180, 0.1) 45%, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.3) 28%, rgba(0, 0, 0, 0.3) 60%, rgba(0, 0, 0, 0.3) 65%, rgba(255, 255, 255, 0.3) 90%, rgba(0, 0, 0, 0.3) 95%, rgba(0, 0, 0, 0.3) 100%);
		/* W3C */
		border-color: rgba(0, 0, 0, 0.6);
		border-image: none;
		border-style: solid;
		border-width: 0px;
		box-shadow: 0 1px 0 0 rgba(255, 255, 255, 0.4) inset, 0 2px 6px rgba(0, 0, 0, 0.5), 0 10px rgba(0, 0, 0, 0.05) inset;
	}

	button:hover.clear-a {
		color: rgba(255, 255, 255, 0.99);
		text-shadow: 0px 0px 2px rgba(255, 255, 255, 0.99), 0px 0px 4px rgba(255, 255, 255, 0.75);
		border-color: rgba(255, 255, 255, 0.4);
		box-shadow: 0 1px 0 0 rgba(255, 255, 255, 0.4) inset, 0 2px 6px rgba(0, 0, 0, 0.5), 0 10px rgba(0, 0, 0, 0.05) inset, 0 0 5px 2px rgba(255, 255, 255, 0.75), 0 0 10px 2px rgba(255, 255, 255, 0.75) inset;
	}

	.small-symbol-view {
		width: 15px;
		height: 15px;
		object-fit: cover;
	}
</style>
