<template>
	<div>
		<div class="scarf_scroll" id="scroll">
			<div class="pro-bar-bg" id="pro-bar-bg" />
			<div class="pro-bar" id="pro-bar" />
			<!-- <img class="pro-bar-bg" draggable="false" oncontextmenu="return false;" :src="require('@/assets/images/scarf/Scarf_UI_RedCloth_Background2.png')" /> -->
			<!-- <img class="pro-bar" draggable="false" oncontextmenu="return false;" :src="require('@/assets/images/scarf/Scarf_UI_RedCloth.png')" /> -->
			<div class="bar" id="bar" @mousedown="mousedown($event)" @mouseup="mousemovecancle($event)"></div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'scarf',
		props:['datas'],
		methods: {
			mousedown(event){
				var scroll = document.getElementById('scroll');
				var bar = document.getElementById('bar');
				bar.style.backgroundColor = "#a98b74";
				let proBar = document.getElementById('pro-bar');
				var event = event || window.event;
				var datas = this.datas;
				//页面事件的X减去当前相对于最近的祖先定位元素
				var x = event.clientX - bar.offsetLeft; //event.currentTarget = bar
				var y = proBar.style.width;
				document.onmousemove = function(event) {
					var event = event || window.event;
					var left = event.clientX - x;
					if (left < 0){
						left = 0;
						bar.style.backgroundColor = "#f8cd86";
						document.onmousemove = null;
					}else if (left > scroll.offsetWidth - bar.offsetWidth) {
						left = scroll.offsetWidth - bar.offsetWidth;
						bar.style.backgroundColor = "#f8cd86";
						document.onmousemove = null;
						// console.log(left/30,left%30)//39.63333333333333 19
					}
					let percent = parseInt(left / (scroll.offsetWidth - bar.offsetWidth) * 100) ;
					// console.log(percent,parseInt(percent/3.333),parseInt(percent/3.333)==0)
					datas.scarf = parseInt(percent/3.333);
					//改变滑块的left
					bar.style.left = left + "px";
					proBar.style.width = bar.style.left;
					// console.log("当前滑块的移动的百分比：" + parseInt(left / (scroll.offsetWidth - bar.offsetWidth) * 100) + "%",scroll.offsetWidth,bar.offsetWidth);
					window.getSelection ? window.getSelection().removeAllRanges() : document.selection.empty();
				}
			},
			mousemovecancle(event){
				var bar = document.getElementById('bar');
				bar.style.backgroundColor = "#f8cd86";
				document.onmousemove = null;
			}
		}
	}
</script>

<style>
	.scarf_scroll {
		height: 100px;
		position: relative;
	}
	.scarf_scroll img{
		/* width: 100%; */
		object-fit: cover;
		padding-top: 20px;
		pointer-events: none;
		user-select: none;
		-ms-user-select: none;
		-moz-user-select: -moz-none;
		-webkit-user-select: none;
	}
	.pro-bar-bg {
		height: 58px;
		width: 100%;
		margin-top: 20px;
		/* padding-top: 20px; */
		background-image: url(../../assets/images/scarf/Scarf_UI_RedCloth_Background2.png);
		background-size: cover;
		background-repeat: no-repeat;
	}
	.pro-bar {
		position: absolute;
		/* left: 0; */
		height: 58px;
		width: 30px;
		margin-top: -58px;
		/* margin-left: -1218px; */
		/* background: #409eff; */
		background-size: cover;
		background-image: url(../../assets/images/scarf/Scarf_UI_RedCloth.png);
		background-repeat: no-repeat;
	}
	.bar {
		width: 30px;
		height: 65px;
		background: #f8cd86;
		position: absolute;
		top: -3px;
		left: 0px;
		cursor: pointer;
		border-radius: 6px;
	}
</style>
