<template>
	<div>
		<div :hidden="!hidden">
			<a class="dropZone">
				<center>
					{{$t("drapText")}}</br>
				</center>
			</a>
			<center style="font-size: 1.5em;">{{$t("savePath")}}</center>
		</div>
		
		<div :hidden="hidden" class="resultZone">
			<a href="#" class="cmatw" @click="hidden = !hidden" >{{$t("cmatw")}}</a>
			<table class="resultTable">
				<tr v-for="i in players.length>4?4:players.length">
					<td v-if="i <= players.length">
						<a class="container" :href="players[i-1].steamLink" target="_blank">
							<img class="symbol" :src="players[i-1].symbolPic"/>
							<div class="players">{{players[i-1].name}}</div>
						</a>
					</td>
					<td v-if="i+4 <= players.length">
						<a class="container" :href="players[i+4-1].steamLink" target="_blank">
							<img class="symbol" :src="players[i+4-1].symbolPic"/>
							<div class="players">{{players[i+4-1].name}}</div>
						</a>
					</td>
				</tr>
			</table>
		</div>
	</div>
</template>

<script>

	// 第一步 dragenter() 是当选中的文件移入到拖拽的区域会触发此方法
	// 第二步 dragover() 是在拖拽的区域不断的移动会触发此方法
	// 第三步 drop() 鼠标在拖拽的区域松开鼠标，释放拖拽的问题会触发此方法
	// 第四步 dragleave() 是拖拽的文件离开拖拽的区域会触发此方法
	
export default {
  name: 'saveparser',
  data () {
	return {
			hidden: true,
			players: []
	    }
  },
  methods: {
		  dropHandler(ev) {
			  this.players = [];
		      const fileReader = new FileReader();
		      fileReader.onload = (callbackEvent) => this.callback(callbackEvent);
		  
		      // console.log('File(s) dropped');
		  
		      // Prevent default behavior (Prevent file from being opened)
			  ev.stopPropagation();
		      ev.preventDefault();
		  
		      if (ev.dataTransfer.items) {
		          // Use DataTransferItemList interface to access the file(s)
		          // If dropped items aren't files, reject them
		          if (ev.dataTransfer.items[0].kind === 'file') {
		              let file = ev.dataTransfer.items[0].getAsFile();
		              fileReader.readAsArrayBuffer(file);
		          }
		      } else {
		          // Use DataTransfer interface to access the file(s)
		          fileReader.readAsArrayBuffer(ev.dataTransfer.files[0]);
		      }
			  this.hidden = !this.hidden
		  },
		  callback(callbackEvent) {
		      const arrayBuffer = callbackEvent.target.result;
		      const baseOffset = 0x19a8;
		      const symbolOffset = 0x1200;
			  const companionsmetOffset = 0x1588;
			  const companionsmet = new DataView(arrayBuffer, companionsmetOffset, 4).getInt32(0, true);
		      for(let i = 0; i < companionsmet; i++) { 
				  //若名字溢出，则会覆盖steamid，导致steamid有误
		          const tempNameBuffer = new Uint8Array(arrayBuffer, baseOffset+32*i, 24);
		          const nameBuffer = tempNameBuffer.subarray(0, tempNameBuffer.indexOf(0x00));
		          const name = new TextDecoder().decode(nameBuffer);
		  
		          const steamIdV3 = new DataView(arrayBuffer, baseOffset+32*i+24, 4).getInt32(0, true);
		          const steamLink = "https://steamcommunity.com/profiles/" + encodeURIComponent(`[U:1:${steamIdV3}]`);
		  
		          const symbolNumber = new DataView(arrayBuffer, symbolOffset+60*i, 4).getInt32(0, true);
		          const symbolPic = require(`@/assets/images/symbol/${symbolNumber}.svg`);
					
		          if (steamIdV3 != 0) { //steamid
					  this.players.push({"name":name,"steamLink":steamLink,"symbolPic":symbolPic})
					  // console.log(name);
					  // console.log(steamLink, steamIdV3);
					  // console.log(symbolPic);
		          }
		      }
		  },
		  dragOverHandler(ev) {
		      // console.log('File(s) in drop zone');
		      // Prevent default behavior (Prevent file from being opened)
			  ev.stopPropagation();
		      ev.preventDefault();
		  }
  },
  mounted () {
	  //https://www.jb51.net/article/224019.htm  基于Vue3的全屏拖拽上传组件
	  const doc = document.documentElement
	  doc.addEventListener('dragleave', (e) => e.preventDefault()) //拖离
	  doc.addEventListener('drop', (e) => e.preventDefault()) //拖后放
	  doc.addEventListener('dragenter', (e) => e.preventDefault()) //拖进
	  doc.addEventListener('dragover', (e) => e.preventDefault()) //拖来拖去
	  
	  let self = this;
	  const ele = document.querySelector('html');
	  ele.addEventListener('drop', (e) => {
		if(!self.hidden) {
			e.stopPropagation();
			e.preventDefault();
			return;
		}
		// console.log("drop");
	    this.dropHandler(e);
	  })
	  ele.addEventListener('dragover', (e) => {
	    this.dragOverHandler(e);
	  })
	  
	  // const files = require.context('%LOCALAPPDATA%\\Annapurna Interactive\\Journey\\Steam',false,/\.BIN$/);
	  // files.keys().forEach(path=>{
		 //  console.log(path);
	  // })
	  
	  // D:\\code\\webui\\journey-save-parser\\datastest\\SAVE.BIN
	  // console.log(this.loadfile("%LOCALAPPDATA%\\Annapurna Interactive\\Journey\\Steam"))

  }
}
</script>

<style>
	td {
	    margin-right: 1em;
	}
	
	.cmatw {
	    /* text-shadow: -2px 0px 20px rgba(173, 162, 126, 0.8); */
		color: white;
		text-decoration:unset;
	}
	
	.cmatw:hover {
	    text-shadow: -2px 0px 20px rgba(173, 162, 126, 0.8);
	    border-radius: 7px;
	    text-decoration: underline;
	}

	.dropZone {
	   /* width: 95%;
	    height: 33%; */

	    display: flex;
	    justify-content: center;
	    align-items: center;
	
	    /* border: 4px dashed #FFFFFF; */
	    /* border-radius: 5px; */
	
	    margin-bottom: 10px;
	    margin-top: 18%;
	
	    font-size: 3em; /* change to scalable unit like vw */
	}
	.resultZone {
	    font-family: 'Patrick Hand SC', cursive;
	    /*font-family: 'Vollkorn SC', serif;*/
	    font-size: 5vh;
	
	    margin-bottom: 10px;
	    margin-top: 6vh;
	    max-width: 90%;
	    margin-left: 18%;
	}
	
	.symbol {
	    width: 1em;
	    margin-right: 0.5em;
	}
	
	.container {
	    display: flex;
	    margin-right: 11vw;
	    color: white;
	    text-decoration: unset;
	}
	
	.container:hover {
	    text-shadow: -2px 0px 20px rgb(173 162 126 / 80%);
	    background-color: #eff2f51f;
	    border-radius: 7px;
	    text-decoration: underline;
	}
	
	.players {
	    display: inline-block;
	    margin: 10px;
	}
	
	.resultTable {
	    border: 0px;
	    font-size: 5vh;
	}

</style>
