<template>
	<table cellspacing="10" cellpadding="10">
		<tr v-for="i in 3">
			<td v-for="j in 7">
				<label class="symbol_card">
					<input class="symbol_card__input" type="checkbox" @click='checkedOne((i==1?j:i==2?j+7:j+14)-1)' :checked="currentId==(i==1?j:i==2?j+7:j+14)-1">
					<div class="symbols symbol_card__body">
						<img class="small-symbol" :src="require(`@/assets/images/symbol/${(i==1?j:i==2?j+7:j+14)-1}.svg`)"/>
					</div>
				</label>
			</td>
		</tr>
	</table>
</template>

<script>
	export default {
		name: 'symbols',
		props: ['datas'],
		data(){
			return {
				currentId:-1,
			}
		},
		methods:{
			checkedOne (id) {
				this.currentId=id;
				this.datas.symbols = id;
			},
		},
	}
</script>

<style>
	.small-symbol {
		width: 80px;
		height: 80px;
		object-fit: cover;
	}

	.table-container {
		overflow-y: auto;
		_overflow: auto;
		margin: 0 0 1em;
	}

	//添加IOS下滚动条
	.table-container::-webkit-scrollbar {
		-webkit-appearance: none;
		width: 14px;
		height: 14px;
	}

	.table-container::-webkit-scrollbar-thumb {
		border-radius: 8px;
		border: 0px solid #fff;
		background-color: rgba(0, 0, 0, .3);
	}
	
	.symbol_card {
	  --background: #fff;
	  /* --background-chackbox: #0082ff; */
	  --background-image: #fff, rgba(0, 107, 175, 0.2);
	  --text-color: #666;
	  --text-headline: #000;
	  --card-shadow: #ffffff;
	  /* --card-height: 200px; */
	  /* --card-witght: 180px; */
	  --card-radius: 12px;
	  --header-height: 47px;
	  --blend-mode: overlay;
	  --transition: 0.15s;
	  -webkit-user-select: none;
	     -moz-user-select: none;
	      -ms-user-select: none;
	          user-select: none;
	}
	.symbol_card__body {
	  /* display: grid;
	  grid-auto-rows: calc(var(--card-height) - var(--header-height)) auto; */
	  height: var(--card-height);
	  width: var(--card-witght);
	  /* border-radius: var(--card-radius); */
	  overflow: hidden;
	  position: relative;
	  cursor: pointer;
	  box-shadow: var(--shadow, 0 30px 30px 0 rgba(0, 0, 0, 0.02));
	  -webkit-transition: box-shadow var(--transition), -webkit-transform var(--transition);
	  transition: box-shadow var(--transition), -webkit-transform var(--transition);
	  transition: transform var(--transition), box-shadow var(--transition);
	  transition: transform var(--transition), box-shadow var(--transition), -webkit-transform var(--transition);
	  -webkit-transform: scale(var(--scale, 1)) translateZ(0);
	          transform: scale(var(--scale, 1)) translateZ(0);
	}
	.symbol_card__body:active {
	  --scale: 0.96;
	}
	.symbol_card__input {
	  position: absolute;
	  display: block;
	  outline: none;
	  border: none;
	  background: none;
	  padding: 0;
	  margin: 0;
	  -webkit-appearance: none;
	}
	.symbol_card__input:checked ~ .symbol_card__body {
	  --shadow: 0 0 0 3px var(--card-shadow);
	}
	.symbol_card__input:disabled ~ .symbol_card__body:active {
	  --scale: 1;
	}
	.symbol_card__input:disabled ~ .symbol_card__body {
	  cursor: not-allowed;
	  opacity: 0.5;
	}
</style>
